import { Component, OnInit } from '@angular/core';
declare var $: any;
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  constructor() { }

  ngOnInit() {  

	  $(document).ready(function(){
	  $('.customer-logos').slick({
	    slidesToShow: 4,
	    slidesToScroll: 1,
	    autoplay: true,
	    autoplaySpeed: 1000,
	    arrows: true,
	    dots: false,
	    pauseOnHover: false,
	    responsive: [{
	      breakpoint: 768,
	      settings: {
	        slidesToShow: 3
	      }
	    }, {
	      breakpoint: 520,
	      settings: {
	        slidesToShow: 1
	      }
	    }]
	  });
	});
  }

}
