import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {RouterModule ,Routes }from '@angular/router';
import { FormsModule }   from '@angular/forms';

import { AppComponent } from './app.component';
import { HeaderComponent } from './component/header/header.component';
import { FooterComponent } from './component/footer/footer.component';
import { SearchPanelComponent } from './component/search-panel/search-panel.component';
import { HomeComponent } from './component/home/home.component';
import { AdvancedSearchComponent } from './component/advanced-search/advanced-search.component';
import { ConfirmBookingComponent } from './component/confirm-booking/confirm-booking.component';
import { GooglePlaceModule } from "ngx-google-places-autocomplete"; 
import { ReactiveFormsModule } from '@angular/forms';
import { HotelDetailsComponent } from './component/hotel-details/hotel-details.component';

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    FooterComponent,
    SearchPanelComponent,
    HomeComponent,
    AdvancedSearchComponent,
    ConfirmBookingComponent,
    HotelDetailsComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    GooglePlaceModule,
    ReactiveFormsModule,
    RouterModule.forRoot([
      { path:'', redirectTo: 'home', pathMatch: 'full' },
      { path:'home', component:HomeComponent },
      { path: 'result', component:AdvancedSearchComponent},
      { path: 'confirm' , component: ConfirmBookingComponent},
      { path: 'details', component: HotelDetailsComponent}
    ])
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
